@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i|Roboto+Condensed:400,700,700i&display=swap);
body {
  height: 100vh;
  width: 100vw;
  background: #F4F6F8;
  color: #414A65;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 100;
  position: fixed;
  background: rgba(255, 255, 255, 0.9); }

h1, h2, h4 {
  font-family: 'Roboto Condensed', sans-serif; }

.product-container {
  min-height: 200px;
  min-width: 500px;
  width: 90%;
  max-width: 1000px;
  padding: 10px;
  margin: 10px;
  border-radius: 3px; }
  .product-container .header {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .product-container .product-item {
    background: #fcfcfc;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    box-shadow: 2px 2px 30px 0px rgba(0, 0, 0, 0.05); }
    .product-container .product-item .item-index {
      margin: 0 20px; }
    .product-container .product-item .product-image {
      max-width: 200px;
      width: 100%;
      height: 100%; }
      .product-container .product-item .product-image svg {
        display: block;
        margin: auto;
        max-width: 70px; }
    .product-container .product-item .product-content {
      padding: 0 20px;
      width: 100%; }
      .product-container .product-item .product-content .content-primary {
        display: flex;
        justify-content: space-between;
        font-family: 'Roboto Condensed', sans-serif; }
        .product-container .product-item .product-content .content-primary .product-title {
          font-weight: 600;
          display: flex;
          max-width: 500px;
          margin: 0; }
        .product-container .product-item .product-content .content-primary .product-price {
          font-weight: 600;
          font-style: italic; }
      .product-container .product-item .product-content .product-description {
        margin: 0;
        font-size: .8rem;
        color: #747474;
        font-family: 'Noto Sans', sans-serif; }
  .product-container .pagination {
    display: flex;
    justify-content: center; }
    .product-container .pagination svg {
      width: 40px;
      fill: #414A65;
      margin: 10px; }

.App {
  display: flex;
  justify-content: center;
  align-items: center; }

@media screen and (max-width: 375px) {
  .product-container {
    margin: none; } }

